<template>
<div>

	<!-- Page Title -->
	<page-title :title="`${username}'s Profile`"></page-title>
	<!-- Notices -->
	<loading-notice v-if="loading">
		<p>We are fetching {{ username }}'s profile</p>
	</loading-notice>

	<missing-notice v-if="missing">
		<p>We cannot locate {{ username }}'s profile.</p>
	</missing-notice>

	<!-- View -->
	<div v-if="!loading && !missing" class="forum">
		<div class="sidebar">
			<div class="habbo-card stats">
				<div class="card">
					<img class="avatar" :src="`https://avatar-retro.com/habbo-imaging/avatarimage?figure=${profile.look}&headonly=1`">
					<div class="meta">
						<h3>{{ profile.username }}</h3>
					</div>
				</div>
			</div>
			<div class="panel gold" style="width:45%;">
				<img src="http://habboo-a.akamaihd.net/c_images/album1584/VipParties3_Top10.gif">
				<p>{{ profile.credits | currency }}</p>
			</div>
			<div class="panel blue" style="width:45%;">
				<img src="http://habboo-a.akamaihd.net/c_images/album1584/GM1.gif">
				<p>{{ profile.points | currency }}</p>
			</div>
			<router-link :to="{ name : 'user.home.profile.home', params : { username : profile.username }}" class="link">
				<p>Home</p>
			</router-link>
			<router-link :to="{ name : 'user.home.profile.guestbook', params : { username : profile.username }}" class="link">
				<p>Guestbook</p>
			</router-link>
			<div class="link">
				<p @click="guestbookModal = true">Add To Guestbook</p>
				<guestbook-modal :user="user.id" :profile="profile.id" v-if="guestbookModal" @close="guestbookModal = false"></guestbook-modal>
			</div>
		</div>
		<div class="view">
			<router-view :profile="profile"></router-view>
		</div>
	</div>
</div>
</template>

<script>
import Guestbook from "./modals/guestbook";
import { HTTP } from "@/app/utilities/http";
export default {
  components: {
    guestbookModal: Guestbook
  },
  data() {
    return {
      user: this.$sess.get("user"),
      username: this.$route.params.username,
      profile: {},
      loading: true,
      missing: false,
      guestbookModal: false
    };
  },
  mounted: function() {
    this.getUser(this.username);
  },
  methods: {
    getUser: function(username) {
      HTTP.get(`users/user/${username}`)
        .then(result => {
          this.profile = result.data;
          this.loading = false;
        })
        .catch(error => {
          this.missing = true;
          this.loading = false;
        });
    }
  },
  watch: {
    "$route.params.username"(username, old) {
      this.getUser(username);
    }
  }
};
</script>
