<template>
<div id="app">
	<router-view></router-view>
</div>
</template>


<script>
import {
	HTTP
} from "@/app/utilities/http";
export default {
	created: function () {
		this.check();
		setInterval(this.check, 300000);
	},
	methods: {
		check: function () {
			HTTP.get("system/cms").catch(() => {
				this.$router.push("/500");
			});
		}
	}
};
</script>
