<template>
<div>
	<div class="habbo-header-large header__top sticky-header sticky-header--top">
		<div class="wrapper">
			<div class="header__top__content" style="margin-top:15px;">
				<div>
					<div class="header__login-form">
						<form @click="error = null" @submit.prevent="login" class="login-form__form" id="loginForm">
							<fieldset class="form__fieldset login-form__fieldset">
								<div class="form__field">
									<input v-model="username" :class="{ 'ng-invalid': error }" class="form__input login-form__input" placeholder="Username">
								</div>
							</fieldset>
							<fieldset class="form__fieldset login-form__fieldset">
								<div class="form__field">
									<input v-model="password" :class="{ 'ng-invalid': error }" class="form__input login-form__input" placeholder="Password" type="password">
								</div>
							</fieldset><button class="login-form__button" type="submit">Let's go!</button>
						</form>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="header__content">
		<div class="register-banner__hotel"></div>
		<div class="register-banner__wrapper">
			<div class="register-banner__register">
				<h1 class="register-banner__logo">Habbo</h1>
				<h2 class="register-banner__title">Make friends &amp; chat in a virtual world of amazement</h2>
				<button @click="register = true" class="register-banner__button">Join for free!</button>
				<register v-if="register" @close="register = false"></register>
			</div>
		</div>
	</div>
	<header class="header__wrapper wrapper">
		<a class="header__habbo__logo" href="/">
			<h1 class="header__habbo__name" id="ga-linkid-habbo-large">Habbo</h1></a>
	</header>
	<heroic-navigation></heroic-navigation>
</div>
</template>

<script>
import JWT from "jwt-decode";
import Register from "@/components/views/guest/register"
import { HTTP } from "@/app/utilities/http";
export default {
	data() {
		return {
			register: false,
			username: null,
			password: null,
			error: null,
		};
	},
	components : {
		'register' : Register
	},
	methods: {
		login: async function () {
			// Clear Errors
			this.error = null;
			// Process
			HTTP.post("users/session", {
					username: this.username,
					password: this.password
				})
				.then(result => {
					// Done
					this.attempting = false
					// Decoded JWT
					const decoded = JWT(result.data);
					this.$sess.set({
						jwt: result.data
					});
					this.$sess.set({
						user: decoded.user
					});
					this.$sess.set({
						session: decoded.session
					});
					this.$router.push("/me");
				})
				.catch(() => {
					// Done
					this.attempting = false
					// Show Error
					this.error = "Your username and password do not match";
				});
		}
	}
};
</script>
